.item {
    text-align: center;
    background-color: lightgrey;
    margin-bottom: 60px;
    margin-right: 30px;
    margin-left: 30px;
    border-radius: 10px;
    border-color: #333;
    box-shadow: 0 5px 10px 0 black;
}

.item:hover {
    cursor: pointer;
    background-color: #333;
    transition: all 0.5s ease-in-out;
    color: whitesmoke;
}

.item_image img {
    max-width: 100%;
    box-shadow: 0 5px 10px 0 black;
}

.project_info {
    display: flex;
    font-weight: 400;
    justify-content: center;
}

.description {
    display: flex;
    justify-content:flex-start;
    align-items: center;
    flex-direction: column;
    margin-left: 15px;
}

.description h4 {
    font-size: 20px;
    margin-top: 10px;
    margin-bottom: 0px;
    text-decoration: underline;
}

.builtWith {
    display: flex;
    width: 40%;
    justify-content:flex-start;
    align-items: center;
    flex-direction: column;
    margin-left: 15px;
}

.builtWith p {
    margin-bottom: 5px;
    margin-top: 5px;
} 

.builtWith h4 {
    font-size: 20px;
    margin-top: 10px;
    margin-bottom: 0px;
    text-decoration: underline;
}

.item_text_description {
    margin-bottom: 0px;
    margin-top: 2px;
}

.item_text_description:last-child {
    margin-bottom: 4px;

}